<template>
  <v-dialog v-model="dialog.show" persistent max-width="780px">
    <v-card>
      <v-card-title>
        <div class="title">
          {{ $vuetify.lang.t(messages.title) }}
        </div>
      </v-card-title>
      <v-divider class="mx-4"></v-divider>
      <v-form @submit.prevent="save">
        <v-card>
          <v-card-text>
            <v-container>
              <v-row wrap>
                <v-col cols="12" sm="3" class="mb-0 py-0">
                  <v-text-field
                    v-model="form.code"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.suppliers.cols.code.title')
                    "
                    prepend-inner-icon="mdi-code-array mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                    readonly
                    filled
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="3" class="mb-0 py-0"> </v-col>

                <v-col cols="12" sm="6" class="mb-0 py-0"> </v-col>
                <v-col cols="12" sm="6" class="mb-0 py-0">
                  <v-text-field
                    v-model="form.name"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.suppliers.cols.name.title')
                    "
                    @blur="$v.form.name.$touch()"
                    @input="$v.form.name.$touch()"
                    :error-messages="nameErrors"
                    prepend-inner-icon="mdi-rename-box mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" class="mb-0 py-0">
                  <v-text-field
                    v-model="form.phone"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.suppliers.cols.phone.title')
                    "
                    @blur="$v.form.phone.$touch()"
                    @input="$v.form.phone.$touch()"
                    :error-messages="phoneErrors"
                    prepend-inner-icon="mdi-phone mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>

                <v-col cols="12" sm="6" class="mb-0 py-0">
                  <v-text-field
                    v-model="form.name_representative"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t(
                        '$vuetify.suppliers.cols.name_representative.title'
                      )
                    "
                    class="font-weight-bold"
                    prepend-inner-icon="mdi-rename-box mdi-dark mdi-18px"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" class="mb-0 py-0">
                  <v-text-field
                    v-model="form.phone_representative"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t(
                        '$vuetify.suppliers.cols.phone_representative.title'
                      )
                    "
                    @blur="$v.form.phone_representative.$touch()"
                    @input="$v.form.phone_representative.$touch()"
                    :error-messages="phoneErrors"
                    prepend-inner-icon="mdi-phone mdi-dark mdi-18px"
                    class="font-weight-bold"
                    outlined
                    dense
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="6"
                  class="mb-0 py-0"
                  style="margin-top: -20px"
                >
                  <v-switch
                    v-model="form.is_active"
                    color="#007bff"
                    :label="
                      $vuetify.lang.t('$vuetify.suppliers.cols.status.enabled')
                    "
                    outlined
                    dense
                  ></v-switch>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-divider class="mx-4" style="margin-top: -25px"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn @click="dialog.show = false">
              {{ $vuetify.lang.t("$vuetify.btn.close") }}
            </v-btn>
            <v-btn color="#007bff" :loading="loading" dark type="submit">
              {{ $vuetify.lang.t(messages.submit) }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
import { required } from "vuelidate/lib/validators";
import { phoneR, existNamesuppliers } from "../../helpers/Validator";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          phone_representative: null,
          name_representative: null,
          code: null,
          phone: null,
          name: null,
          is_active: false
        };
      }
    }
  },
  data: () => ({
    formData: {
      id: null,
      name: null
    },
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    urlItems: "suppliers",
    isLoading: false
  }),
  watch: {},

  created() {
    //this.setItemRoles();
  },
  validations() {
    let validators = {
      form: {
        name: {
          required,
          unique: existNamesuppliers
        },
        phone: {
          phoneR
        },
        phone_representative: {
          phoneR
        }
      }
    };
    return validators;
  },
  methods: {
    // Fonction pour rcuperer les types de roles dans la store
    // Fonction pour rcuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        name_representative: this.form.name_representative,
        phone_representative: this.form.phone_representative,
        phone: this.form.phone,
        name: this.form.name,
        is_active: this.form.is_active ? this.form.is_active : false
      };
      return data;
    },
    setForm(item) {
      // Donnes envoyes lors de la modification

      this.form.id = item.id;
      this.form.code = item.code;
      this.form.name = item.name;
      this.form.name_representative = item.name_representative;
      this.form.phone = item.phone;
      this.form.phone_representative = item.phone_representative;
      this.form["oldName"] = item.name;
      this.form.is_active = item.is_active;
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;

      !this.$v.form.name.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.suppliers.cols.name.required")
        );
      !this.$v.form.name.unique &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.suppliers.cols.name.unique")
        );
      return errors;
    },
    codeErrors() {
      let errors = [];
      if (!this.$v.form.code.$dirty) return errors;
      !this.$v.form.code.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.suppliers.cols.code.required")
        );

      !this.$v.form.code.maxLength &&
        errors.push(
          this.$vuetify.lang.t(
            "$vuetify.suppliers.cols.code.maxLength",
            this.$v.form.code.$params.maxLength.max
          )
        );
      return errors;
    },
    phoneErrors() {
      let errors = [];
      if (!this.$v.form.phone.$dirty) return errors;
      !this.$v.form.phone.phoneR &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.suppliers.cols.phone.invalid")
        );
      return errors;
    }
  },
  components: {}
};
</script>
